import { Component, Output, EventEmitter } from '@angular/core';
import { Family } from '../services/modeles/model_catalalogue_Family';
import { Brand } from '../services/modeles/model_produits_Marque';
import { RefProduit } from '../services/modeles/model_produits_RefProduit';
// import { ServiceManager } from '../services/servicemanager/service.manager';
import { PubSubService } from '../services/userinputs/pubsubservice';
import { UserDevice } from '../services/modeles/user_device.model';

import {AppService} from '../appServices/appServices' ;

@Component({
  selector: 'product-mod',
  templateUrl: 'app/modals/product-mod.component.html'
})

export class ProductModal {
  @Output() closeModal = new EventEmitter();
  @Output() newProduct:EventEmitter<RefProduit> = new EventEmitter<RefProduit>();

  private errorMessage: string = "";
  private isWaranty: boolean = false;

  isOn: boolean = false;

  constructor(public appService: AppService, private pubSubService:PubSubService) {
    
  }

  isWaranted(): void {
    this.isWaranty = !this.isWaranty;
    console.log(this.isWaranty);
  }
 
  
  OnClick(event:MouseEvent): void {
    event.stopPropagation();
  }

  toggleOn(): void {
    this.isOn = true;
  }

  toggleOff(): void {
    if (this.isOn == true) {
      this.closeModal.emit({});
    }
    this.isOn = false;
  }

  onValid(): void {
    
    this.appService.saveCurrentDevice();

    if (this.isOn == true) {
      this.newProduct.emit(this.appService.appDatas.deviceSelector.selectedRefProduit);
      this.closeModal.emit({});
    }
    this.isOn = false;

  }

  
}



