import { Component } from '@angular/core';
import { CordonProgress } from '../layout/cordon-progress.component';
import { PubSubService } from '../services/userinputs/pubsubservice';
import { ROUTER_DIRECTIVES , Router , RouteParams } from '@angular/router-deprecated';
import { panneDescription } from '../services/modeles/panne-description';
// import { ServiceManager } from '../services/servicemanager/service.manager';
import { Address } from '../services/modeles/model_address';
import { UserProfile } from '../services/modeles/user_model';

import {AppService} from '../appServices/appServices' ;

@Component({
  selector: 'device',
  templateUrl: 'app/device/device.component.html',
  directives: [CordonProgress, ROUTER_DIRECTIVES]
})

export class Device {

  constructor(private pubSubService:PubSubService, public appService: AppService, private router: Router , private params:RouteParams){
  }

  ngOnInit(){
    if(this.params.get("id")){
        this.appService.appDatas.initUserDeviceEdition(this.params.get("id")) ;
    }else{
        this.appService.appDatas.initUserDeviceEdition("" );
    }
  }

  onValid(){
    this.appService.saveCurrentDevice();
    this.router.navigate(["Mes-appareils"])
  }
   
  
}



