
import {Injectable} from '@angular/core';
import {UserData} from './userData' ;
import {InfoService} from './infoService' ;
import {AppDatas} from './appDatas' ;
import {AppConfig} from '../config/appConfig' ;
import { panneDescription } from '../services/modeles/panne-description';
import { Observable } from 'rxjs/Rx';
import { Address } from '../services/modeles/model_address';
import { FailureFamily } from '../services/modeles/panne-description';
import  * as _ from 'lodash';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class AppService {



constructor(public userData: UserData  , public infoService: InfoService  , public appDatas: AppDatas , public appConfig: AppConfig ) {

}


public saveCurrentDevice():void{
  if(this.appDatas.currentUserDevice.id){
    this.userData.updateDevice(this.appDatas.currentUserDevice)  
  }else{
    this.appDatas.currentUserDevice.product = this.appDatas.deviceSelector.selectedRefProduit ;
    this.userData.addDevice(this.appDatas.currentUserDevice)  
  }
}

public saveCurrentRma(generateEtiquette:boolean=false):Observable<panneDescription>{
  if(this.appDatas.currentPanne.id){
    this.appDatas.currentPanne.user = this.userData.profile ;
    this.appDatas.currentPanne.failure = this.appDatas.failureSelector.selectedFailure ;
    this.appDatas.currentPanne.failureFamilyId = this.appDatas.failureSelector.selectedFailureFamily.id;
    this.appDatas.currentPanne.device = this.appDatas.userDeviceSelector.selectedUserDevice ;
    this.userData.saveRma(this.appDatas.currentPanne);
    if(generateEtiquette){
      this.userData.generateRmaLabel(this.appDatas.currentPanne) ;
    }
    return new BehaviorSubject<panneDescription>(this.appDatas.currentPanne).asObservable()
  }else{
    this.appDatas.currentPanne.user = this.userData.profile ;
    this.appDatas.currentPanne.failure = this.appDatas.failureSelector.selectedFailure ;
    this.appDatas.currentPanne.failureFamilyId = this.appDatas.failureSelector.selectedFailureFamily.id ;

    this.appDatas.currentPanne.device = this.appDatas.userDeviceSelector.selectedUserDevice ;
    let obs:Observable<panneDescription> = this.infoService.getRepairCenter(this.appDatas.currentPanne.device.product)
    .flatMap( 
      data => {
        this.appDatas.currentPanne.repairCenterAddress = data ;
        return this.userData.saveNewRma(this.appDatas.currentPanne) ;
      }
    ).map(
      data =>{
        this.appDatas.currentPanne = data ;
        this.appDatas.newPanne = null ;
        return data
      }
    ).cache()
      obs.subscribe(
      data => {
      }
     )

    return obs
  }
  
}

// retro compatibilité
public saveNewRma():Observable<panneDescription>{
  console.log("obsolete use saveCurrentRma") ;
  return this.saveCurrentRma(false);
  
}




}


