
import {Injectable} from '@angular/core';
import {UserData} from './userData' ;
import {AppConfig} from '../config/appConfig' ;

import { CordonServiceFamilies } from '../services/service_catalalogue_Families';
import { CordonServiceFailures } from '../services/service_catalalogue_failures';
import { Exapaq } from '../services/exapaq';

import { Observator } from '../decorators/Observator';

// Import data models
import { Family } from '../services/modeles/model_catalalogue_Family';
import { Brand } from '../services/modeles/model_produits_Marque';
import { RefProduit } from '../services/modeles/model_produits_RefProduit';
import { ExapaqPointRelais } from '../services/modeles/exapaqPointRelais';
import { panneDescription, Failure, FailureFamily } from '../services/modeles/panne-description';
import { Address } from '../services/modeles/model_address';

import { Observable } from 'rxjs/Rx';

import { DeviceSelector } from './componantServices/deviceSelector';
import { FailureSelector } from './componantServices/failureSelector';
import { UserDeviceSelector } from './componantServices/userDeviceSelector';
import { UserDevice } from '../services/modeles/user_device.model';

@Injectable()
export class AppDatas {


// devices
protected _deviceSelector:DeviceSelector ;
public get deviceSelector ():DeviceSelector{
  if(!this._deviceSelector){
    this._deviceSelector = new DeviceSelector(this._families , this.userData ) 
  }
  return this._deviceSelector ;
}


//user Device

//UserDevice seletor
protected _userDeviceSelector:UserDeviceSelector ;
public get userDeviceSelector ():UserDeviceSelector{
  if(!this._userDeviceSelector){
    this._userDeviceSelector = new UserDeviceSelector(this.userData ) 
  }
  return this._userDeviceSelector ;
}

private _newUserDevice:UserDevice ;
public get newUserDevice():UserDevice{
  if(!this._newUserDevice){
    this._newUserDevice = new UserDevice( ) 
  }
  return this._newUserDevice ;
}
public set newUserDevice(value:UserDevice){
  this._newUserDevice = value ;
} 


private _currentUserDevice:UserDevice ;
public get currentUserDevice():UserDevice{
  // if(!this._currentUserDevice){
  //   this._currentUserDevice = this.newUserDevice() ;
  // }
  return this._currentUserDevice ;
}
public set currentUserDevice(value:UserDevice){
  if(this._currentUserDevice != value){
    this._currentUserDevice = value ;
    if(value.id){
      this.userDeviceSelector.selectedUserDeviceId = value.id ;
    }else{
      this.userDeviceSelector.selectedUserDeviceId = "" ;
    }
    
  }
  
} 

public get currentUserDeviceId():string{
  if(!this._currentUserDevice){
    return this._currentUserDevice.id
  }
  return "";
}
public set currentUserDeviceId(value:string){
  if(value == ""){
    this.currentUserDevice = new UserDevice() ;
  }else{
    this.userDeviceSelector.selectedUserDeviceId = value ;
    this.currentUserDevice = this.userDeviceSelector.selectedUserDevice ;
  }
} 

public initUserDeviceEdition(id:string):void{
  this.currentUserDeviceId = id ;
  if(this.currentUserDevice.product){
    this.deviceSelector.selectedFamilyId = this.currentUserDevice.product.familyId;
    this.deviceSelector.selectedBrandId = this.currentUserDevice.product.brandId;
    this.deviceSelector.selectedRefProduitId = this.currentUserDevice.product.id;
  }
  
}

// panne
protected _failureSelector:FailureSelector ;
public get failureSelector ():FailureSelector{
  if(!this._failureSelector){
    this._failureSelector = new FailureSelector(this._failures , this.userData ) 
  }
  return this._failureSelector ;
}

private _currentPanne:panneDescription ;
public get currentPanne():panneDescription{
  
  return this._currentPanne ;
}
public set currentPanne(value:panneDescription){
  if(this._currentPanne != value){
    this._currentPanne = value ;
    
    if(value && value.failure){
      if(value.failureFamilyId && value.failureFamilyId) {
        this.failureSelector.selectedFailureFamilyId = value.failureFamilyId;
      }else{
        this.failureSelector.selectedFailureFamilyId = "" ;
      }
      this.failureSelector.selectedFailureId = value.failure.id ;
    }else{
      this.failureSelector.selectedFailureId = "" ;
    }
    if(value && value.device){
      this.userDeviceSelector.selectedUserDeviceId = value.device.id ;
    }else{
      this.userDeviceSelector.selectedUserDeviceId = "" ;
    }
  }
} 

public set currentPanneId(id:string){
  if(this.userData.rmas){
    if(id && id != ""){
      this.currentPanne = this.userData.rmas.find(
      data => data.id == id
      )
      
    }else{
      this.currentPanne = this.newPanne ;
    }
    
  }else{
    this.currentPanne = this.newPanne ;
    this.userData.initRma().subscribe(
      data => {
        if(data){
          this.currentPanneId = id
        }
        
      }
      )
  }
  
}
public get currentPanneId():string {
  if(this.currentPanne){
    return this.currentPanne.id
  }else{
    return ""
  }
}

private _newPanne:panneDescription ;
public get newPanne():panneDescription{
  if(!this._newPanne){
    this._newPanne = new panneDescription( ) 
  }
  return this._newPanne ;
}
public set newPanne(value:panneDescription){
  this._newPanne = value ;
} 


// addresses
private _newAddress:Address ;
public get newAddress():Address{
  if(!this._newAddress){
    this._newAddress = new Address( ) 
  }
  return this._newAddress ;
}
public set newAddress(value:Address){
  this._newAddress = value ;
} 

constructor( private _families: CordonServiceFamilies,private _failures: CordonServiceFailures , private appConfig: AppConfig , private userData:UserData) {

        
}






}


