import { Component, Input } from '@angular/core';
import { ExapaqPointRelais } from '../services/modeles/exapaqPointRelais';
import { GOOGLE_MAPS_DIRECTIVES } from 'angular2-google-maps/core/index';

@Component({
  selector: 'location-map',
  templateUrl: 'app/prisencharge/location-map.component.html',
  directives: [GOOGLE_MAPS_DIRECTIVES],
  styles: [`
  .sebm-google-map-container {
    height: 100%;
  }
  `]
})

export class LocationMap {
  @Input() public locations: ExapaqPointRelais[] = [];

  zoom: number = 12;
  latitude: number = 48.8588377;
  longitude: number = 2.2775174;

  ngOnInit() {
    this.zoom = 12;
    console.log ("map Init: " + this.latitude + this.locations);
  }

  public setLocations(_locations: ExapaqPointRelais[]):void {
    this.locations = _locations;
    if (this.locations)
    {
      this.latitude = this.getMapCenter().lat;
      this.longitude = this.getMapCenter().lng;
      this.zoom = this.getZoomLvl();
      // console.log (this.latitude);
      // console.log (this.longitude);
    }
  }

  // calulculate map center based on the markers list
  private getMapCenter(): latlng {
    let center: latlng = {lat: 0, lng: 0};

    // coordonnées cartésiennes du centre de la carte à calculer
    let sumX: number = 0;
    let sumY: number = 0;
    let sumZ: number = 0;

    // variables de support pour la boucle
    let tmpLat: number = 0;
    let tmpLng: number = 0;

    for (var i = 0; i < this.locations.length; i++)
    {
      tmpLat = this.locations[i].latitude * Math.PI / 180;
      tmpLng = this.locations[i].longitude * Math.PI / 180;

      sumX += Math.cos(tmpLat) * Math.cos(tmpLng);
      sumY += Math.cos(tmpLat) * Math.sin(tmpLng);
      sumZ += Math.sin(tmpLat);
    }

    sumX = sumX / this.locations.length;
    sumY = sumY / this.locations.length;
    sumZ = sumZ / this.locations.length;

    // convert average x, y, z coordinate to latitude and longtitude
    tmpLng = Math.atan2(sumY, sumX);
    let hyp:number = Math.sqrt(sumX * sumX + sumY * sumY);
    tmpLat = Math.atan2(sumZ, hyp);

    center.lat = tmpLat * 180 / Math.PI;
    center.lng = tmpLng * 180 / Math.PI;

    return center;
  }

  // adapt zoom level
  private getZoomLvl(): number
  {
    // Based on the Haversine formula, calculate the distance of the nearest and farest points
    let earthR: number = 6371;
    let midLat: number = (this.locations[this.locations.length - 1].latitude - this.locations[0].latitude) * Math.PI/180;
    let midLng: number = (this.locations[this.locations.length - 1].longitude - this.locations[0].longitude) * Math.PI/180;

    let a: number = Math.sin(midLat/2)*Math.sin(midLat/2) +
                    Math.cos(this.locations[0].latitude * Math.PI/180) * Math.cos(this.locations[this.locations.length - 1].latitude * Math.PI/180) +
                    Math.sin(midLng/2)*Math.sin(midLng/2);

    let c: number = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));

    let scaleDist: number = earthR * c;

    console.log (scaleDist)
    if (scaleDist < 5000)
    {
      return 12;
    }
    else if (scaleDist < 10000)
    {
      return 10;
    }
    else return 8;

  }

}

interface latlng {
  lat: number;
  lng: number;
}



