// Angular 2 imports
import { Component } from '@angular/core';
import { RouteConfig, ROUTER_DIRECTIVES, ROUTER_PROVIDERS } from '@angular/router-deprecated';

// imports layouts
import { CordonNav } from './layout/cordon-nav.component';
import { CordonHome } from './layout/cordon-home.component';
import { CordonFooter } from './layout/cordon-footer.component';

// imports pages
import { Panne } from './panne/panne.component';
import { PriseEnCharge } from './prisencharge/prise-en-charge.component';
import { PriseEnChargeHome } from './prisencharge/prise-en-charge.home';
import { ValidationAdress } from './validationadresse/validation.adress';
import { MyDevicesComponent } from './myprofile/mydevices.component';
import { MyDataComponent } from './myprofile/mydata.component';
import { MyPannesComponent } from './myprofile/mypannes.component';
import { RecapRma } from './recap/recap.component';
import { Device } from './device/device.component';

//import models
import { panneDescription } from './services/modeles/panne-description';
import { RefProduit } from './services/modeles/model_produits_RefProduit';

// import services
import { PubSubService } from './services/userinputs/pubsubservice';
// import { ServiceManager } from './services/servicemanager/service.manager';
import { Exapaq } from './services/exapaq';
import { CordonUserProfile } from './services/service_user_Profile';
import { CordonServiceFamilies } from './services/service_catalalogue_Families';
import { CordonServiceMarques } from './services/service_produits_Marques';
import { CordonServiceFailures } from './services/service_catalalogue_failures';
import { CordonServiceRMA } from './services/service_rma';
import { CordonServiceRepairCenter } from './services/service_repairCenter';

import { CordonServiceSuggestedProducts } from './services/service_suggestedProducts';
import { CordonServicePrice } from './services/service_price';

import {Observable} from 'rxjs/Observable';
import { AppService , UserData ,  InfoService , AppDatas} from './appServices/all';
import { AuthService} from './auth/authService';
import { AppConfig} from './config/appConfig';

import {Http, Response, Headers, RequestOptions, RequestOptionsArgs, Request} from '@angular/http';


@Component({
  selector: 'my-app',
  template: `
    <cordon-nav></cordon-nav>
    <router-outlet></router-outlet>
    <cordon-footer></cordon-footer>`,
  directives: [CordonNav, CordonFooter, ROUTER_DIRECTIVES],
  providers: [ROUTER_PROVIDERS, PubSubService,  
              Exapaq,   CordonServiceFamilies, CordonServiceMarques, CordonUserProfile
              , CordonServiceFailures, CordonServiceRMA , CordonServiceRepairCenter
              , CordonServiceSuggestedProducts , CordonServicePrice
              , UserData , AppService,  InfoService, AppDatas
              , AppConfig 
              , AuthService ]

})

@RouteConfig([
  {
    path: '/',
    name: 'Home',
    component: CordonHome
  },
  {
    path: '/device',
    name: 'Description-Device-New',
    component: Device
  },
  {
    path: '/device/:id',
    name: 'Description-Device',
    component: Device
  },
  {
    path: '/panne',
    name: 'Description-Panne-New',
    component: Panne
  },
  {
    path: '/panne/:id',
    name: 'Description-Panne',
    component: Panne
  },
  {
    path: '/prise-en-charge/:id',
    name: 'Prise-en-charge',
    component: PriseEnCharge
  },
  {
    path: '/prise-en-charge-home/:id',
    name: 'Prise-en-charge-home',
    component: PriseEnChargeHome
  },
  {
    path: '/validation-adresse/:id',
    name: 'Validation-adresse',
    component: ValidationAdress
  },
  {
    path: '/mon-compte',
    name: 'Mon-compte',
    component: MyDataComponent
  },
  {
    path: '/mes-appareils',
    name: 'Mes-appareils',
    component: MyDevicesComponent
  },
  {
    path: '/mes-pannes',
    name: 'Mes-pannes',
    component: MyPannesComponent
  },
  {
    path: '/recapitulatif/:id',
    name: 'Recapitulatif',
    component: RecapRma
  }
])

export class AppComponent {
  userDevice: RefProduit;
  userPanne: panneDescription;
  userTransport: string;

  constructor(private pubSubService: PubSubService ) {
  }

  ngOnInit(){
    this.pubSubService.deviceEmit.subscribe(device => this.processDevice(device));
    this.pubSubService.panneEmit.subscribe(panne => this.processPanne(panne));
    this.pubSubService.transportEmit.subscribe(transport => this.processTransport(transport));
  }

  processDevice(device: RefProduit): void {
    this.userDevice = device;
  }

  processPanne(panne: panneDescription): void {
    this.userPanne = panne;
    console.log("panne:" + JSON.stringify(this.userPanne) );
  }

  processTransport(transport: string): void {
    this.userTransport = transport;
    console.log(this.userTransport);
  }
}



