import { Component, ViewChild } from '@angular/core';
// import { ServiceManager } from '../services/servicemanager/service.manager' ;
import { UserProfile } from '../services/modeles/user_model';
import { UserDevice } from '../services/modeles/user_device.model';
import { RefProduit } from '../services/modeles/model_produits_RefProduit';
import { Family } from '../services/modeles/model_catalalogue_Family';
import { Brand } from '../services/modeles/model_produits_Marque';
import { ProductModal } from '../modals/product-mod.component';
import { ConfirmModal } from '../modals/confirm.modal';
import {AppService} from '../appServices/appServices' ;
import { ROUTER_DIRECTIVES , Router} from '@angular/router-deprecated';
import  * as _ from 'lodash';

@Component({
  selector: 'mydevices',
  templateUrl: 'app/myprofile/mydevices.component.html',
  directives: [ProductModal, ConfirmModal, ROUTER_DIRECTIVES]
})

export class MyDevicesComponent {

  private oldUserData: UserProfile ;
  private errorMessage: string;
  private isModalActive: boolean = false;
  private delProdId: string = "";

  public isReady:boolean ;

  @ViewChild(ProductModal) productMod: ProductModal;
  @ViewChild(ConfirmModal) confirmMod: ConfirmModal;

  constructor(public appService: AppService){
    this.appService.appDatas.currentUserDeviceId = "" ;
    this.appService.userData.obs_profile.subscribe(data =>{
      this.createBackup()
    })
  }

  protected createBackup():void{
    if(this.appService.userData.profile){
      this.oldUserData = _.cloneDeep(this.appService.userData.profile ) ;
      this.isReady = true ;
    }else{
      this.oldUserData = null ;
      this.isReady = false ;
    }
  }

  

  // any displayed modal emits an event "closeModal"
  closeModal(event): void {
    this.isModalActive = false;
    if (event == 'none') { //closing new device modal
      this.productMod.toggleOff();
    } else { //closing confirm device delete modal
            if (event == true) {
              this.OnDeleteProduct();
            }
    }
  }

  // user clicked to add a new device
  NewProduct(): void {
    
    this.isModalActive = true;
    this.productMod.toggleOn();
  }

  private onNewProduct(event): void {
    console.log("on new");
    
    // this.updateProfileData();
  }

  // User clicked on product trash icon
  DeleteProduct(idProd: string): void {
    this.isModalActive = true;
    this.delProdId = idProd;
    this.confirmMod.toggleOn();
  }

  // User Confirmed deletion after popin display
  OnDeleteProduct(): void {

    console.log("delete product")
    this.appService.userData.deleteDevice(this.delProdId)
  }

}



