import { Component, ViewChild } from '@angular/core';

import { CordonProgress } from '../layout/cordon-progress.component';
import { LocationList } from './location-list.component';
import { LocationMap } from './location-map.component';
import { Exapaq } from '../services/exapaq';
import { ExapaqPointRelais } from '../services/modeles/exapaqPointRelais';
import { Observable } from 'rxjs/Observable';
import { RetourModal } from '../modals/retour-mod.component';
// import { ServiceManager } from '../services/servicemanager/service.manager';
import {AppService} from '../appServices/appServices' ;

@Component({
  selector: 'prise-en-charge',
  templateUrl: 'app/prisencharge/prise-en-charge.component.html',
  directives: [CordonProgress, LocationList, LocationMap, RetourModal]
  // providers: [Exapaq]
})
export class PriseEnCharge {
  public zipCode = "";
  locationList: ExapaqPointRelais[];
  errorMessage = "OK";
  isModalActive: boolean = false;

  @ViewChild(RetourModal) retMod: RetourModal;
  @ViewChild(LocationMap) locationMap: LocationMap;

  constructor( public appService: AppService){
    if(this.appService.userData.profile.addresses && this.appService.userData.profile.addresses.length>0){
        this.zipCode = this.appService.userData.profile.addresses[0].zipCode
    }
    
  }
  ngOnInit() {
    this.setLocations();
  }

  handleLocation($event): void {
    console.log($event);
    this.isModalActive = true;
    this.retMod.toggleOn();
  }

  closeModal(): void {
    this.isModalActive = false;
    this.retMod.toggleOff();
  }



  // call this on new zipCode inputs
  setLocations(): void {
    if(this.zipCode != ""){
      this.appService.infoService.getLocations(this.zipCode, "", "").subscribe(
        _locationList => this.locationList = _locationList,
        error => this.errorMessage = "echec d'appel WEBSERVICE"
      );
    }
    
  }
}



