import { EventEmitter, Injectable } from '@angular/core';
import { RefProduit } from '../modeles/model_produits_RefProduit';
import { panneDescription } from '../modeles/panne-description';
import { ExapaqPointRelais } from '../modeles/exapaqPointRelais';

@Injectable()

export class PubSubService{

   public deviceEmit: EventEmitter<RefProduit>;
   public panneEmit: EventEmitter<panneDescription>;
   public relaisEmit: EventEmitter<ExapaqPointRelais>;
   public transportEmit: EventEmitter<string>;
   public loggingEmit: EventEmitter<boolean>;

   constructor(){
       this.deviceEmit = new EventEmitter<RefProduit>();
       this.panneEmit = new EventEmitter<panneDescription>();
       this.relaisEmit = new EventEmitter<ExapaqPointRelais>();
       this.transportEmit = new EventEmitter<string>();
       this.loggingEmit = new EventEmitter<boolean>();
   }
}



