
import { Family } from '../../services/modeles/model_catalalogue_Family';
import { Brand } from '../../services/modeles/model_produits_Marque';
import { RefProduit } from '../../services/modeles/model_produits_RefProduit';

import { Observable } from 'rxjs/Rx';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observator } from '../../decorators/Observator';
import { CordonServiceFamilies } from '../../services/service_catalalogue_Families';
import {UserData} from '../userData' ;


export class DeviceSelector {


  @Observator
  public families:Family[];
  public obs_families:Observable<Family[]> ;

  @Observator
  public brands:Brand[];
  public obs_brands:Observable<Brand[]> ;

  @Observator
  public refProduits:RefProduit[];
  public obs_refProduits:Observable<RefProduit[]> ;




  protected _selectedFamily:BehaviorSubject<Family> = new BehaviorSubject(null) ;
  public get selectedFamily():Family {
    return this._selectedFamily.getValue() ;
  }
  public set selectedFamily(value:Family) {
    if(this.selectedFamily != value ){
      this._selectedFamily.next(value) ;
      this._selectedBrand.next(null) ;
      this._selectedRefProduit.next(null) ;
      this.getBrands(value.id).subscribe(
        data => {
          this.brands = data ;
        }
        )
    }
  }
  public obs_selectedFamily:Observable<Family> = this._selectedFamily.asObservable();

  public get selectedFamilyId():string{

    if(this.selectedFamily){
      return this.selectedFamily.id ;
    }else{
      return 
    }
  }
  public set selectedFamilyId(id:string){

    this.selectedFamily = this.families.find(
       familly => familly.id == id
       ) 
  }


  protected _selectedBrand:BehaviorSubject<Brand> = new BehaviorSubject(null);
  public get selectedBrand():Brand {
    return this._selectedBrand.getValue() ;
  }
  public set selectedBrand(value:Brand) {
    if(this._selectedBrand.getValue() != value ){
      this._selectedBrand.next(value) ;
      this._selectedRefProduit.next(null) ;
      this.getDevices(this.selectedFamily.id, value.id).subscribe(
        data => {
          this.refProduits = data ;
        }
        )
    }
  }
  public obs_selectedBrand:Observable<Brand> = this._selectedBrand.asObservable();

  public get selectedBrandId():string{
    if(this.selectedBrand){
      return this.selectedBrand.id ;
    }else{
      return ""
    }
  }
  public set selectedBrandId(id:string){
    if(this.brands){
        this.selectedBrand = this.brands.find(
         brand => brand.id == id
         )
    }else{
      this.obs_brands.subscribe(
        data =>{
          if(data){
            this.selectedBrandId = id ;
          }
        })
    }
  }

  protected _selectedRefProduit:BehaviorSubject<RefProduit> = new BehaviorSubject(null);
  public get selectedRefProduit():RefProduit {
    return this._selectedRefProduit.getValue() ;
  }
  public set selectedRefProduit(value:RefProduit) {
    if(this._selectedRefProduit.getValue() != value ){
      this._selectedRefProduit.next(value) ;
      
    }
  }
  public obs_selectedRefProduit:Observable<RefProduit> = this._selectedRefProduit.asObservable();

  public get selectedRefProduitId():string{
    if(this.selectedRefProduit){
      return this.selectedRefProduit.id ;
    }else{
      return ""
    }
  }
  public set selectedRefProduitId(id:string){
    if(this.refProduits){
      this.selectedRefProduit = this.refProduits.find(
       refProduit => refProduit.id == id
       ) 
    }else{
      this.obs_refProduits.subscribe(
        data =>{
          if(data){
            this.selectedRefProduitId = id ;
          }
        })
    }

  }

  constructor(private _families: CordonServiceFamilies ,  private userData:UserData ) {
    this.getFamilies().subscribe(
      data =>{
        this.families = data
      }
      )
  }





  public getToken():Observable<string>{
    return this.userData.getToken()
  }


  protected getFamilies(): Observable<Family[]> {
    return this.getToken()
      .flatMap( token => this._families.getFamily('', token) ) ;
  }

  protected getBrands(_idFamily: string): Observable<Brand[]> {
    console.log (_idFamily);
    return this.getToken()
      .flatMap( token => this._families.getMarques(_idFamily , token));
  }

  protected getDevices(_idFamily: string, _idBrand: string): Observable<RefProduit[]> {
    return this.getToken()
      .flatMap( token => this._families.getProducts(_idFamily, _idBrand  , token ));
  }

}


