
import {Injectable} from '@angular/core';
import {UserData} from './userData' ;
import {AppConfig} from '../config/appConfig' ;

import { CordonServiceFamilies } from '../services/service_catalalogue_Families';
import { CordonServiceFailures } from '../services/service_catalalogue_failures';
import { CordonServiceRepairCenter } from '../services/service_repairCenter';
import { Exapaq } from '../services/exapaq';

import { Observator } from '../decorators/Observator';

// Import data models
import { Family } from '../services/modeles/model_catalalogue_Family';
import { Brand } from '../services/modeles/model_produits_Marque';
import { RefProduit } from '../services/modeles/model_produits_RefProduit';
import { ExapaqPointRelais } from '../services/modeles/exapaqPointRelais';
import { panneDescription, Failure, FailureFamily } from '../services/modeles/panne-description';
import { Address } from '../services/modeles/model_address';


import { Observable } from 'rxjs/Rx';

@Injectable()
export class InfoService {


@Observator
public family:Family;
public obs_family:Observable<Family> ;

@Observator
public brand:Brand;
public obs_brand:Observable<Brand> ;

@Observator
public refProduit:RefProduit;
public obs_refProduit:Observable<RefProduit> ;

@Observator
public failureFamily:FailureFamily[];
public obs_failureFamily:Observable<FailureFamily[]> ;

@Observator
public exapaqPointRelais:ExapaqPointRelais;
public obs_exapaqPointRelais:Observable<ExapaqPointRelais> ;



constructor(private _exapaq: Exapaq, private _families: CordonServiceFamilies,private _failures: CordonServiceFailures, private _repaireCenter:CordonServiceRepairCenter , private appConfig: AppConfig , private userData:UserData) {
         _exapaq.url = this.appConfig.services.exapaq ;
        _families.url = this.appConfig.services.families ;
         _failures.url = this.appConfig.services.failures ;
         _repaireCenter.url = this.appConfig.services.repairCenterUrl ;
}

public getToken():Observable<string>{
  return this.userData.getToken()
}


///////////////////////////////////////////////////////////////
  // Exapaq transport and adresses utilities
  ///////////////////////////////////////////////////////////////
  getLocations(zipCode: string, city: string, address: string): Observable<ExapaqPointRelais[]> {
    if (zipCode && zipCode.length>2){
      
      return this.getToken()
      .flatMap( token => this._exapaq.getPointRelais(zipCode, city, address , token));
     }else{
       return null ;
    }
  }

  ///////////////////////////////////////////////////////////////
  // DEVICES UTILITIES
  ///////////////////////////////////////////////////////////////

  getFamilies(): Observable<Family[]> {
    return this.getToken()
      .flatMap( token => this._families.getFamily('', token) ) ;
  }

  getBrands(_idFamily: string): Observable<Brand[]> {
    console.log (_idFamily);
    return this.getToken()
      .flatMap( token => this._families.getMarques(_idFamily , token));
  }

  getDevices(_idFamily: string, _idBrand: string): Observable<RefProduit[]> {
    return this.getToken()
      .flatMap( token => this._families.getProducts(_idFamily, _idBrand  , token ));

  }

  public initFailureFamily():void{
    if(!this.failureFamily){
      this.getPannesFamilies().subscribe(
      data => {
        this.failureFamily = data
      }
      )
    }
    
  }
  getPannesFamilies(): Observable<FailureFamily[]> {
    return this.getToken().flatMap( token => this._failures.getFailureFamily(token));
  }

  getRepairCenter(device: RefProduit): Observable<Address> {
   return this.getToken().flatMap( token => this._repaireCenter.getRepairCenter(device, token) );
 }

}


