import { Component, ViewChild } from '@angular/core';
import { ROUTER_DIRECTIVES } from '@angular/router-deprecated';

// Modals imports
import { SignUpModal } from '../modals/signup-mod.component';
import { LoginModal } from '../modals/login-mod.component';

// import {ServiceManager} from '../services/servicemanager/service.manager' ;
import {AppService} from '../appServices/appServices' ;
import { Observable } from 'rxjs/Observable';
// import {DROPDOWN_DIRECTIVES} from 'ng2-semantic-ui/ng2-semantic-ui';

@Component({
  selector: 'cordon-nav',
  templateUrl: 'app/layout/cordon-nav.component.html',
  directives: [SignUpModal, LoginModal, ROUTER_DIRECTIVES]
})

export class CordonNav {

  public isLogged:boolean = false;
  public isMeOver:boolean = false;
  private isModalActive: boolean = false;
  public firstName:string = "Marwan";
  public lastName:string = "Mazloum";

  @ViewChild(SignUpModal) suMod: SignUpModal;
  @ViewChild(LoginModal) loMod: LoginModal;

  //constructor( private serviceManager:ServiceManager) {
  constructor( public appService:AppService) {

    
  }

  OnMouseEnterMe():void {
    this.isMeOver = true;
  }

  OnMouseLeaveMe():void {
    this.isMeOver = false;
  }

  closeModal(): void {
    this.isModalActive = false;
    this.loMod.toggleOff();
    this.suMod.toggleOff();
  }

  OnLogin(): void {
    this.appService.userData.initImplicitFlow();
  }

  SignUp(): void {
    this.appService.userData.initImplicitFlow();
  }

  logOut():void{
    this.appService.userData.logOut();
  }
}



