


import { Observable } from 'rxjs/Rx';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observator } from '../../decorators/Observator';



import {UserData} from '../userData' ;

import { UserDevice } from '../../services/modeles/user_device.model';

export class UserDeviceSelector {


  @Observator
  public userDevices:UserDevice[];
  public obs_failureFamily:Observable<UserDevice[]> ;


  protected _selectedUserDevice:BehaviorSubject<UserDevice> = new BehaviorSubject(null) ;
  public get selectedUserDevice():UserDevice {
    return this._selectedUserDevice.getValue() ;
  }
  public set selectedUserDevice(value:UserDevice) {
    if(this.selectedUserDevice != value ){
      this._selectedUserDevice.next(value) ;
    }
  }
  public obs_selectedUserDevice:Observable<UserDevice> = this._selectedUserDevice.asObservable();

 public get selectedUserDeviceId():string{

    if(this.selectedUserDevice){
      return this.selectedUserDevice.id ;
    }else{
      return 
    }
  }
  public set selectedUserDeviceId(id:string){

    this.selectedUserDevice = this.userDevices.find(
       device => device.id == id
       ) 
  }

  

  constructor( private userData:UserData ) {
    this.userData.obs_profile.subscribe(
      data =>{
        console.log(data)
        this.userDevices = data.devices
      }
      )
  }





  

}


