


import { Observable } from 'rxjs/Rx';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observator } from '../../decorators/Observator';

import { panneDescription, Failure, FailureFamily } from '../../services/modeles/panne-description';
import { CordonServiceFailures } from '../../services/service_catalalogue_failures';

import {UserData} from '../userData' ;


export class FailureSelector {


  @Observator
  public failureFamily:FailureFamily[];
  public obs_failureFamily:Observable<FailureFamily[]> ;

  @Observator
  public failures:Failure[];
  public obs_failures:Observable<Failure[]> ;

  protected _selectedFailureFamily:BehaviorSubject<FailureFamily> = new BehaviorSubject(null) ;
  public get selectedFailureFamily():FailureFamily {
    return this._selectedFailureFamily.getValue() ;
  }
  public set selectedFailureFamily(value:FailureFamily) {
    if(this.selectedFailureFamily != value ){
      this._selectedFailureFamily.next(value) ;
      if(value){
        if(value.failures){
          this.failures = value.failures ; 
        }else{
          this.failures = [] ; 
        }
        }
      
      
    }
  }
  public obs_selectedFamily:Observable<FailureFamily> = this._selectedFailureFamily.asObservable();
  
  protected initSelectedFailureFamilyId:string = "" ;
  public get selectedFailureFamilyId():string{
    if(this.selectedFailureFamily){
      return this.selectedFailureFamily.id ;
    }else{
      return 
    }
  }
  public set selectedFailureFamilyId(id:string){
    if(this.failureFamily){
      this.selectedFailureFamily = this.failureFamily.find(
       familly => familly.id == id
       ) 
    }else{
      this.initSelectedFailureFamilyId = id ;
    }
    
  }


  protected _selectedFailure:BehaviorSubject<Failure> = new BehaviorSubject(null) ;
  public get selectedFailure():Failure {
    return this._selectedFailure.getValue() ;
  }
  public set selectedFailure(value:Failure) {
    if(this.selectedFailure != value ){
      this._selectedFailure.next(value) ;
      
      
    }
  }
  public obs_selectedFailure:Observable<Failure> = this._selectedFailure.asObservable();

  protected initSelectedFailureId:string = "" ;
  public get selectedFailureId():string{
    if(this.selectedFailure){
      return this.selectedFailure.id ;
    }else{
      return 
    }
  }
  public set selectedFailureId(id:string){
    if(this.failures){
      this.selectedFailure = this.failures.find(
       failure => failure.id == id
       )
    }else{
      this.initSelectedFailureId = id;
    }
    
  }


  

  constructor(private _failures: CordonServiceFailures ,  private userData:UserData ) {
    this.getPannesFamilies().subscribe(
      data =>{
        console.log(data)
        this.failureFamily = data ;
        this.selectedFailureFamilyId = this.initSelectedFailureFamilyId ;
        this.selectedFailureId = this.selectedFailureId ;
      }
      )
  }





  public getToken():Observable<string>{
    return this.userData.getToken()
  }


 getPannesFamilies(): Observable<FailureFamily[]> {
    return this.getToken().flatMap( token =>  this._failures.getFailureFamily(token));
 }

}


