import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ExapaqPointRelais } from '../services/modeles/exapaqPointRelais';

@Component({
  selector: 'location-list',
  templateUrl: 'app/prisencharge/location-list.component.html',
})

export class LocationList {
  @Input() public locations: ExapaqPointRelais[];
  @Output() public locationSelect = new EventEmitter();

  SelectLocation(location: ExapaqPointRelais): void {
    console.log (location);
    this.locationSelect.emit({newValue: location});
  }
}



