import {Injectable}     from '@angular/core';
import {Http, Response, Headers, RequestOptions, RequestOptionsArgs, Request} from '@angular/http';
import {Observable}     from 'rxjs/Rx';
import {Family}     from './modeles/model_catalalogue_Family';
import { Brand } from './modeles/model_produits_Marque';
import { RefProduit } from './modeles/model_produits_RefProduit';

@Injectable()
export class CordonServiceFamilies {

 constructor(private http: Http ) { }
 public url: string
 getFamily(term: String , token:string): Observable<Family[]> {
   let headers = new Headers({ 'Content-Type': 'application/json', 'Accept' :'application/json', 'Authorization': "Bearer " + token  });
   let options = new RequestOptions({ headers: headers });

  //  console.log(this.url + "/families/");
   return this.http.get(this.url + "/families/product/0/10")
     .map(this.extractData)
     .catch(this.handleError);
 }

 getMarques(idFamille: String, token:string): Observable<Brand[]> {
  //  console.log("callUsers")
   let headers = new Headers({ 'Content-Type': 'application/json', 'Accept' :'application/json'  , 'Authorization': "Bearer " + token });
   let options = new RequestOptions({ headers: headers });

  //  console.log(this.url + "/brands/" + idFamille);
   return this.http.get(this.url + "/brands/" + idFamille)
     .map(this.extractData)
     .catch(this.handleError);
 }

 getProducts(idFamille: String , idBrand:String , token:string): Observable<RefProduit[]> {
   let headers = new Headers({ 'Content-Type': 'application/json', 'Accept' :'application/json'  , 'Authorization': "Bearer " + token });
   let options = new RequestOptions({ headers: headers });

   return this.http.get(this.url + "/products/" + idFamille + "/" + idBrand)
     .map(this.extractData)
     .catch(this.handleError);
 }

 upsertFamily(user: Family , token:string): Observable<Family[]> {
   let body = JSON.stringify(user);
   let headers = new Headers({ 'Content-Type': 'application/json', 'Accept' :'application/json' , 'Authorization': "Bearer " + token});
   let options = new RequestOptions({ headers: headers });

   return this.http.post(this.url, body, options)
     .map(this.extractData)
     .catch(this.handleError);
 }

 deleteFamily(user: Family , token:string): Observable<Family[]> {
   let body = JSON.stringify(user);
   let headers = new Headers({ 'Content-Type': 'application/json', 'Accept' :'application/json', 'Authorization': "Bearer " + token });
   let options = new RequestOptions({ headers: headers });

   return this.http.delete(this.url, options)
     .map(this.extractData)
     .catch(this.handleError);
 }

 private extractData(res: Response) {
  // console.log("extractData" + JSON.stringify(res.json()))
  //  if (res.status < 200 || res.status >= 300) {
  //    throw new Error('Bad response status: ' + res.status);
  //  }
   let body = res.json();
  //  if (body.code |= 200) {
  //    console.error(body.message);
  //    Observable.throw(body.message)
  //  }
   return body.results || [];
 }
 private handleError(error: any) {
   // In a real world app, we might send the error to remote logging infrastructure
   let errMsg = error.message || 'Server error';
   console.error(errMsg); // log to console instead
   return Observable.throw(errMsg);
 }

}



