import { Component } from '@angular/core';
import { CordonProgress } from '../layout/cordon-progress.component';
import { PanneDescription } from './panne-description.component';
import { DeviceSelect } from './device-select.component';
import { PubSubService } from '../services/userinputs/pubsubservice';
import { UserDevice } from '../services/modeles/user_device.model';
import { panneDescription } from '../services/modeles/panne-description';
// import { ServiceManager } from '../services/servicemanager/service.manager';
import { Address } from '../services/modeles/model_address';
import { UserProfile } from '../services/modeles/user_model';
import { Router , RouteParams } from '@angular/router-deprecated';
import {AppService} from '../appServices/appServices' ;
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'panne',
  templateUrl: 'app/panne/panne.component.html',
  directives: [CordonProgress, PanneDescription, DeviceSelect]
})

export class Panne {
  // private panne: panneDescription = new panneDescription();
  // private device: UserDevice = new UserDevice();

  constructor(private globalEvt: PubSubService, public appService: AppService, private router: Router, private routeParams:RouteParams){
    console.log(router);
  }

  ngOnInit(){
    if(this.routeParams.get("id")){
      this.appService.appDatas.currentPanneId = this.routeParams.get("id") ;
    }else{
      this.appService.appDatas.currentPanneId = "" ;
    }
  }

  onPanneDesc(panne: panneDescription): void {

    if(this.appService.appDatas.currentPanne.id){
      this.appService.saveCurrentRma() ;
      this.router.navigate(['Prise-en-charge-home' , {id:this.appService.appDatas.currentPanneId}]);
    }else{
      this.appService.saveNewRma().subscribe(
        data =>{
          this.router.navigate(['Prise-en-charge-home' , {id:this.appService.appDatas.currentPanneId}]);
        }
      )
    }
    
  }




}



