import {Injectable} from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import {UserProfile}     from '../services/modeles/user_model';
import { panneDescription } from '../services/modeles/panne-description';
import { Identity } from '../services/modeles/identity_model';
import { Observator } from '../decorators/Observator';

import {JwtHelper, AuthHttp, AuthConfig, AUTH_PROVIDERS} from 'angular2-jwt';
import {Http, Response, Headers, RequestOptions, RequestOptionsArgs, Request} from '@angular/http';

import { CordonUserProfile } from '../services/service_user_Profile';
import { CordonServiceRMA } from '../services/service_rma';

import { RefProduit } from '../services/modeles/model_produits_RefProduit';
import { Address } from '../services/modeles/model_address';
import { UserDevice } from '../services/modeles/user_device.model';

import { AuthService } from '../auth/authService';

import {AppConfig} from '../config/appConfig' ;

@Injectable()
export class UserData {
	
  // identity server data (sso)
  @Observator
  public identity:Identity;
  public obs_identity:Observable<Identity> ;


  // user profile (sso)
  @Observator
  public profile:UserProfile;
  public obs_profile:Observable<UserProfile> ;

  protected saveProfilePending:boolean ;
  public  saveProfile():void{
    console.log("save profile") ;
    let _that = this ;
    this.upDateProfile(this.profile).subscribe(
      data => {
        console.log( "profile updated " + data) ;
        this.profile = data ;
      }
      )
  }

  public addDevice(device:UserDevice):void{
    this.AddUserDevice(device ).subscribe(
      data => {
        console.log( "device added " , data) ;
        this.profile = data ;
      }
      )
  }
  public updateDevice(device:UserDevice):void{
    this.UpdateUserDevice(device ).subscribe(
      data => {
        
        
      }
      )
  }
  public deleteDevice(deviceId:string):void{
    this.DeleteUserDevice(deviceId).subscribe(
      data => {
        console.log( "device deleted " , data) ;
        this.profile = data ;
      }
      )
  }

  public isLogged:boolean ;
  // user rmas 
  @Observator
  public rmas:panneDescription[];
  public obs_rmas:Observable<panneDescription[]> ;

  public initRma():Observable<panneDescription[]>{
    if(!this.rmas){
      this.getPannes().subscribe(
        data =>{
          this.rmas = data ;
        }
        )
    }
    return this.obs_rmas ;
  }

  // public currentPanne: panneDescription = new panneDescription();

  protected saveRmaPending:boolean ;
  public  saveRma(rma:panneDescription):void{
    this.putPanne(rma) ;
  }

  public  saveNewRma(rma:panneDescription):Observable<panneDescription>{
    
    return this.postPanneRMA(rma).map(data =>{
      this.rmas.push(data) ;
      return data ;
    }) ;
  }
  public  generateRmaLabel(rma:panneDescription):Observable<panneDescription>{
    return this.addLabel(rma) ;
  }


  
  public  deleteRma(rmaId:string):void{
    this.deletePanne(rmaId).subscribe(
      data=>{
        console.log("deleted") ;
      })
  }

  constructor( private authService:AuthService , private http: Http, private appConfig: AppConfig, private _user: CordonUserProfile , private _rma: CordonServiceRMA) {
          // this._identityObs =  new BehaviorSubject(null) ;
          // this.obs_identity =  this._identityObs.asObservable();

       

        _user.url = this.appConfig.services.user ;
        _rma.url = this.appConfig.services.rma ;

          this.obs_profile.subscribe( (userProfile) => {
            if(userProfile){
              this.isLogged = true ;
            }
            
          })

          authService.obs_context.subscribe(
            data =>{
              if(data){
                this.initTocken(data);
              }   
            }) 
        
  }



 public getToken():Observable<string>{
   if(this.identity){
     if(this.jwtHelper.isTokenExpired(this.identity.jwt)  ){
       console.log("token expire")
       return this.http.get(this.appConfig.sso.jwtUrl + this.accesstoken , new RequestOptions({ }))
       .map(this.extractData)
     }else{
       console.log("token ok")
       return new BehaviorSubject(this.identity.jwt).asObservable()
     }
   }else{
     console.log("rebond vars sso")
     this.initImplicitFlow()
   }
 }
  //autentification sequence

private jwtHelper: JwtHelper = new JwtHelper();
protected accesstoken:string ;

private initTocken(context: any) {
  console.log("onTokenReceived" + context)

  let options = new RequestOptions({ });
  this.accesstoken = context.accessToken ;

  return  this.http.get(this.appConfig.sso.jwtUrl + context.accessToken , options)
    .map(resp => this.extractData(resp))
    .flatMap(
      data =>this.userExist()
      )
    .map(
        userExist => {
          console.log("userExist : "+userExist)
          if(userExist){
            return this.getProfile().subscribe(  data => {
                    this.profile = data
                  });
          }else{
            return this.createProfile().subscribe(  data => {
                    this.profile = data
                  });
          }
        }
      )
    .catch(this.handleError)
    .subscribe(  data => {
      this.profile = data

      // data.subscribe( res => {
      //     console.log("profile" + res )
      //     this.profile = res
      //   }
      // )
    });
}

private extractData(res: Response) {
  console.log(res.text())
  let jsonToken = this.jwtHelper.decodeToken(res.text());
  // console.log("jWT", jsonTocken)
  let myIdentity: Identity = new Identity();
  myIdentity.id = jsonToken.sub ;
  myIdentity.lastName = jsonToken.family_name ;
  myIdentity.firstName = jsonToken.given_name ;
  myIdentity.email =  jsonToken.email ;
  myIdentity.jwt =  res.text() ;
  
  this.identity = myIdentity ;

  // console.log("extractData " + this.token)
  return res.text() || "";
} 

private handleError(error: any) {
    // In a real world app, we might send the error to remote logging infrastructure
    let errMsg = error.message || 'Server error';
    console.error(errMsg); // log to console instead
    return Observable.throw(errMsg);
  }

public initImplicitFlow(){
  // this.oauthService.initImplicitFlow();
  this.authService.initImplicitFlow() ;
}
public logOut():void{
  this.identity = null;
  this.profile = null ;
  this.rmas = null ;
}



  //fin d'autent


//user api
createProfile(): Observable<UserProfile> {

  let newProfile:UserProfile = new UserProfile() ;
  newProfile.lastName = this.identity.lastName;
  newProfile.firstName = this.identity.firstName;
  newProfile.email = this.identity.email;
  newProfile.id = this.identity.id;

  return this.getToken().flatMap( token => this._user.createProfile(newProfile , this.identity.jwt) );
}


userExist(): Observable<UserProfile> {
    return this.getToken().flatMap( token => this._user.userExist(this.identity.id, token)).cache();
  }
getProfile(): Observable<UserProfile> {
    console.log("getProfile from service manager");
    return this.getToken().flatMap( token => this._user.getProfile(this.identity.id, token)).cache();
  }

upDateProfile(profile:UserProfile): Observable<UserProfile> {
  console.log("hello upDateProfile");
  return this.getToken().flatMap( token => this._user.updateProfile(profile, token)).cache();
}


AddUserDevice(device:UserDevice): Observable<UserProfile> {
  return this.getToken().flatMap( token => this._user.addDevice(this.identity.id, device , token)).cache();
}
UpdateUserDevice(device:UserDevice): Observable<UserProfile> {
  return this.getToken().flatMap( token => this._user.updateDevice(this.identity.id, device , token)).cache();
}
DeleteUserDevice(deviceId:string): Observable<UserProfile> {
  return this.getToken().flatMap( token => this._user.deleteDevice(this.identity.id, deviceId, token)).cache();
}



//rma api
getPannes(): Observable<panneDescription[]> {
  
  return this.getToken().flatMap( token => this._rma.getPannes(this.identity.id, token)).cache();
}
postPanneRMA(panne: panneDescription): Observable<panneDescription> {
  
  return this.getToken().flatMap( token => this._rma.postPanneRMA(panne, token)).cache();
}

putPanne(panne: panneDescription): Observable<panneDescription> {

  return this.getToken().flatMap( token => this._rma.putPanneRMA(panne, token)).cache();
}

deletePanne(rmaId): Observable<panneDescription> {

  return this.getToken().flatMap( token => this._rma.deletePanneRMA(rmaId, token)).cache();
}


addLabel(panne: panneDescription): Observable<panneDescription> {

  return this.getToken().flatMap( token => this._rma.addLabel(panne, token)).cache();
}





}


