import {Injectable} from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import {UserProfile}     from '../services/modeles/user_model';
import { panneDescription } from '../services/modeles/panne-description';
import { Identity } from '../services/modeles/identity_model';

export  function Observator (target: any, propertyKey: string | symbol) {
       let _value = target[propertyKey.toString()] ;
       let _obs =  new BehaviorSubject(null) ;
       target["obs_" + propertyKey.toString()] =  _obs.asObservable();
        function getter() {
          return _value;
        }

        function setter(newValue) {
          if(_value != newValue){
            _value = newValue;
            _obs.next(_value) ;
          }
        }

        if(delete target[propertyKey.toString()]) {
          Object.defineProperty(target, propertyKey.toString(), {
              get: getter,
              set: setter
          });
        }

}


