import { ROUTER_DIRECTIVES } from '@angular/router-deprecated';

import { Component, Output, EventEmitter } from '@angular/core';
import { CordonProgress } from '../layout/cordon-progress.component';
import { RefProduit } from '../services/modeles/model_produits_RefProduit';
import { panneDescription, Failure, FailureFamily } from '../services/modeles/panne-description';
// import { ServiceManager } from '../services/servicemanager/service.manager';
import {AppService} from '../appServices/appServices' ;

import * as _ from 'lodash';

@Component({
  selector: 'panne-description',
  templateUrl: 'app/panne/panne-description.component.html',
  directives: [CordonProgress, ROUTER_DIRECTIVES]
})

export class PanneDescription {

  panneDesc: panneDescription = new panneDescription();
  deviceIn: RefProduit;
  panneFamilies: FailureFamily[];
  failures: Failure[];
  panneFamilly: string;
  panneCategory: string;

  @Output() panneDescEvt: EventEmitter<panneDescription> = new EventEmitter<panneDescription>();
  @Output() onValid: EventEmitter<void> = new EventEmitter<void>();

  constructor(public appService: AppService) {

    
  }

 

  onPanneChange():void {
    // this.appService.appDatas.newPanne.failure = this.appService.appDatas.failureSelector.selectedFailure ;
  }

  onSendPanne():void {
    this.panneDescEvt.emit(this.panneDesc);
  }

}



